/* Middle part */

section.middlebar {
    background-color: #111111;
    border-bottom: 3px solid whitesmoke;
    height: 1750px; 
    width: 60%;
    max-width: 1200px;
    min-width: 120px; 
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%,0%);
    padding: 0.01em 16px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
    align-items: center;
}

div.gamedesc {
    position: relative;
    height: 50px; 
    width: 95%; 
    background-color: #232222; 
    border-radius: 4px; 
    margin-top: 50px;
    text-align: center; 
    font-size: 20px; 
    box-shadow: 0.5rem 0.5rem 0.5rem #0a0a0a;
    padding: 0.01em 16px;
}

@media only screen and (max-width: 1500px) {
    div.gamedesc {
        height: 35px;
    }
  }

div.gamedesc button {
    cursor: pointer;
    position: relative;
    float: left;
    font-family: 'Press Start 2P';
    font-size: 14px;
    background-color: #232222;
    color: white;
    height: 100%;
    width: 25%;
    border: none;
    transition: 400ms;
}

@media only screen and (max-width: 1500px) {
    div.gamedesc button {
        font-size: 12px;
    }
  }

div.gamedesc button:hover {
    background-color:#0a0a0a
}

@media only screen and (max-width: 900px) {
    section.middlebar {
        width: 100%;
    }
}

nav.promoinfo {
    height: 22.3%;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    align-items: center;
    justify-content: space-around;
}

nav.promoinfo p {
    font-size: 12px;
    font-family: 'Nunito';
    font-weight: bold;
}

@media screen and (max-width: 1450px) {
    nav.promoinfo p {
        font-size: 8px;
    }
  }

@media screen and (max-width: 1100px) {
    nav.promoinfo p {
        font-size: 5px;
    }
  }


nav.promoinfo div {
    height: 90%;
    max-height: 35px;
    width: 65%;
    box-shadow: 0px 2px 2px #0a0a0a; 
    background-image: linear-gradient(to right, rgb(5, 86, 17), rgb(3, 172, 3) );   
}





/* left part */

section.leftbar {
    background-color: #111111;  
    height: 1750px; 
    width: 20%;
    min-width: 40px;  
    position: absolute;
    left: 0;
    top: 0;
    display: block;
}

@media only screen and (max-width: 900px) {
    section.leftbar {
        visibility: hidden;
    }
  }

  section.leftbar {
    background-color: #111111; 
    height: 1750px; 
    width: 20%; 
    border-bottom: 3px solid whitesmoke;
    position: absolute;
    display: flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: nowrap;
}

section.leftbar nav {
    float: top;
    height: 200px; 
    width: 200px; 
    margin-top: 25px; 
    text-align: center; 
    font-size: 20px;
    position: relative;
}

@media only screen and (max-width: 1400px) {
    section.leftbar nav {
        height: 150px;
        width: 150px;
    }
  }

section.leftbar img {
    height: 100%; 
    width: 100%;
}

hr {
    width: 95%;
}

section.leftbar div h3 {
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    color: rgb(1, 129, 1);
}

section.leftbar div p {
    font-family: 'Nunito';
}

@media only screen and (max-width: 1400px) {
    section.leftbar div p  {
        font-size: 10px;
        
    }
  }

/* rightbar part */

section.rightbar {
    background-color: #111111;
    border-bottom: 3px solid whitesmoke;
    height: 1750px; 
    width: 20%;
    min-width: 40px;   
    position: absolute;
    right: 0;
    top: 0;
}

@media only screen and (max-width: 900px) {
    section.rightbar {
        visibility: hidden;
    }
  }