div.gamecard {
    position: relative;
    box-shadow: 0.5rem 0.5rem 0.5rem #0a0a0a;
    height: 400px;
    width: 925px;
    background-color: #232222;
    margin-top: 25px;
    border: solid 2px #302e2e;
    border-radius: 5px;
    transition: 1000ms;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    align-content: space-around;
}

@media screen and (max-width: 1750px) {
    div.gamecard {
        height: 333px; 
        width: 770px;
    }
}

@media screen and (max-width: 1450px) {
    div.gamecard {
        height: 266px; 
        width: 616px;
    }
}

@media screen and (max-width: 1100px) {
    div.gamecard {
        height: 200px; 
        width: 465px;
    }
}

@media screen and (max-width: 500px) {
    div.gamecard {
        height: 160px; 
        width: 370px;
    }
}